

<!---->
<template >
    <div>
        <!---->
        <div class="table-container">

            <!---->
            <div class="user-text">
                <img class="image" src="src/assets/img/.png" height="25" width="25"/>
                <span></span>
            </div>

            <!---->
            <input type="button" class="btn-general create-user" value=""  @click="openDialog()">

            <!---->
            <table class="table-box">
                <!---->
                <tr>
                    <th ></th>
                    <th ></th>
                    <th ></th>
                    <th></th>
                </tr>

                <!---->
                <tr v-for="(item,index) in user_data"> <!--v-for-->
                    <td>
                        {{item.user_name}}
                    </td>

                    <td>
                        {{item.account}}
                    </td>

                    <td>
                        <input type="password" class="input-pwd" value="********" readonly>
                    </td>

                    <td>
                        <input type="button" class="btn-general" value="" @click="openCha(index)">    <!--v-on click index-->
                        <input type="button" class="btn-general" value="" @click="deleteUsers(index)">
                    </td>
                </tr>
            </table>
        </div>


        <!---->
        <div class="dialogs" v-bind:show.sync="show_create">
            <div class="dialog" v-bind:class="{ 'dialog-active': show_create }">
                <div class="dialog-content">

                    <header class="dialog-header">
                        <span class="dialog-title"></span>
                        <span class="close rotate iconfont icon-close" @click="closeDialog()"></span><!---->
                    </header>

                    <div class="dialog-body" >
                        <div class="strap">
                            <span class="span-motal"></span>
                            <input type="text"  id="text1" class="input-motal" placeholder="2-10" onfocus="this.placeholder=''" @blur="checkIn('text1','2-10','^[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0)"  v-model="user_name" v-bind:class="{warnBorder:this.is_active[0]}">
                        </div>

                        <div>
                            <span class="span-motal"></span>
                            <input type="text"  id="text2" class="input-motal"  placeholder="8-10" onfocus="this.placeholder=''" @blur="checkIn('text2','8-10','^[a-zA-Z0-9]{8,10}$',1)" v-model="account" v-bind:class="{warnBorder:this.is_active[1]}">
                        </div>

                        <div>
                            <span class="span-motal"></span>
                            <input type="password"  id="text3" class="input-motal" placeholder="6-16"  onfocus="this.placeholder=''" @blur="checkIn('text3','6-16','^[a-zA-Z0-9_]{6,16}$',2)" v-model="pwd" v-bind:class="{warnBorder:this.is_active[2]}">
                        </div>

                        <div>
                            <span class="span-motal"></span>
                            <input type="password" id="text4" class="input-motal" placeholder="" onfocus="this.placeholder=''" @blur="checkPwd('text3','text4')" v-model="confirm_pwd" v-bind:class="{warnBorder:this.is_active[3]}">
                        </div>
                    </div>
                    <footer class="dialog-footer" >
                        <!---->
                        <p  class="warn-tip" v-model="tip">{{tip}}</p>
                        <!---->
                        <div>
                            <button class="btn-general" style="margin-right: 10%" @click="closeDialog()"></button>
                            <button class="btn-general "  @click="addMsg()"></button>
                        </div>
                    </footer>
                </div>
            </div>
            <div class="dialog-overlay"></div>
        </div>


        <!---->
        <div class="dialogs" v-bind:show.sync="show_change">
            <div class="dialog" v-bind:class="{ 'dialog-active': show_change }">
                <div class="dialog-content">

                    <header class="dialog-header">
                        <span class="dialog-title"></span>
                        <span class="close rotate iconfont icon-close" @click="closeCha()"></span><!---->
                    </header>

                    <div class="dialog-body">
                        <div>
                            <span class="span-motal">:</span>

                            <input type="text" name="user_name" id="chan1" class="input-motal" v-model="back_account" readonly style="color: dimgrey;font-size: 15px;font-weight: 400;border-color: transparent; ">
                        </div>

                        <div>
                            <span class="span-motal">:</span>

                            <input type="text"  id="chan2" class="input-motal" v-model="back_username" @blur="checkIn('chan2','2-10','^[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0)" v-bind:class="{warnBorder:this.is_active[0]}">
                        </div >

                        <div>
                            <span class="span-motal">:</span>
                            <input type="password"  id="chan3" class="input-motal" placeholder="6-16"  onfocus="this.placeholder=''" @blur="checkIn('chan3','6-16','^[a-zA-Z0-9_]{6,16}$',2)" v-model="pwd"  v-bind:class="{warnBorder:this.is_active[2]}" >
                        </div>

                        <div>
                            <span class="span-motal">:</span>
                            <input type="password" id="chan4" class="input-motal" placeholder="" onfocus="this.placeholder=''" @blur="checkPwd('chan3','chan4')" v-model="confirm_pwd" v-bind:class="{warnBorder:this.is_active[3]}">
                        </div>
                    </div>

                    <footer class="dialog-footer" slot="footer">
                        <!---->
                        <span class="warn-tip" v-model="tip">{{tip}}</span>
                        <div>
                            <button class="btn-general" style="margin-right: 10%" @click="closeCha()"></button>
                            <button class="btn-general" @click="confirmChange()"></button>
                        </div>
                    </footer>
                </div>
            </div>
        </div>

        <page-component  :cur_max=this.max_page v-on:pre_page="preP" v-on:next_page="nextP" v-on:skip_page="skiP"></page-component>
    </div>
</template>


<script type="text/javascript">
    import pageComponent from "./pageComponent.vue"

    export default{
        components:{ pageComponent },
        data(){
            return {
                show_create: false,
                show_change: false,

                tip: '',//
                is_active:[false,false,false,false], //
                add_if:true, // / 

                user_data: [],
                user_name: '',
                account: '',
                pwd: '',
                confirm_pwd: '',
                back_index: '',
                back_username: '',
                back_account: '',
                get_url: '/ancient_books/get_user_list.action',//
                add_url: '/ancient_books/add_user.action',//
                modify_url: '/ancient_books/modify_user.action',//
                delete_url: '/ancient_books/delete_user_by_id.action',//

                page: 1,//
                max_page: 3,//

                get_user: {},//
                add_user:{},//
                delete_user:{},//
                modify_user:{},//
                check_name:{}, //
                check_account:{} //
            }
        },
        created: function () {
            console.log("super_user created");
            this.getUsers(1);
            console.log("createdtoken"+this.Token);
        },
        methods: {
            // get 
            successGet(response){
                    console.log("success get users ");
                    //
                    if( response.body.content.length === 0 ){
                        console.log("");
                    }else {
                        this.max_page = response.body.max_page;
                        for ( let i = 0; i < response.body.content.length; i++ ){
                            this.user_data.push({
                                user_name: response.body.content[i].name,
                                account: response.body.content[i].account,
                                user_id: response.body.content[i].user_id
                            });
                        }
                    }
            },

            failGet(){
                console.log("fail get users!");
            },

            //
            getUsers(pages){
                this.get_user.key="page";
                this.get_user.value=pages;
                this.HttpGetForm(this.get_url,this.get_user,this.successGet, this.failGet);
            },

            // post success
           successAdd(response){
                if(response.body.result===1) {
                    console.log("success create!");
                    this.cleanData();
                    this.show_create = false; //
                    /*this.userData.push({   //
                        user_name: this.user_name,
                        account: this.account,
                        pwd: this.pwd,
                        user_id: response.body.user_id
                    });*/
                    // this.info_total=this.userData.length;
                    //this.page=(this.info_total+ this.info_num-1)/this.info_num;

                }else if (response.body.result===0) {
                    console.log("fail create!");
                }
            },

            failAdd(){
                console.log("error create!");
            },

            //post
            createUsers(){
                //
                this.add_user.name=this.user_name;
                this.add_user.account=this.account;
                this.add_user.pwd=this.pwd;
                this.HttpPostForm(this.add_url,this.add_user,this.successAdd,this.failAdd);
                /*this.$http.post(this.add_url,this.add_user,{
                    emulateJSON: true   //jsonform-data
                },{headers:{'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8'}}).then(function (response) { //
                    this.success_add(response);
                    this.AfterSuccess(response);
                },function () {
                    this.fail_add();
                })*/
            },

            // 
            openDialog(){
                this.show_create = true;
                this.user_name="";
                this.account="";
                this.pwd="";
                this.confirm_pwd="";
                this.tip="";
                this.is_active[0]=false;
                this.is_active[1]=false;
                this.is_active[2]=false;
                this.is_active[3]=false;
            },

            closeDialog() {
                this.show_create = false;
            },

            //
            checkIn(tag1,placeholder_text,juge_text,index){
                let x=document.getElementById(tag1).value;
                if(x!==""){
                    if(x.match(juge_text)){
                        this.tip="";
                        document.getElementById(tag1).placeholder =placeholder_text;
                        this.add_if=true;
                        this.is_active[0]=false;
                        this.is_active[1]=false;
                        this.is_active[2]=false;
                    }else{
                        this.tip=placeholder_text;
                        document.getElementById(tag1).placeholder = placeholder_text;
                        this.add_if=false;
                        this.is_active[index]=true;
                    }
                }else{
                    document.getElementById(tag1).placeholder =placeholder_text;
                    this.add_if=false;
                    this.is_active[0]=false;
                    this.is_active[1]=false;
                    this.is_active[2]=false;
                }
            },

            //
            checkPwd(tag1,tag2){
                let x=document.getElementById(tag1).value;
                let y=document.getElementById(tag2).value;
                if(y!==""){
                    if(y!==x){
                        this.tip="";
                        document.getElementById(tag2).placeholder = "";
                        this.add_if=false;
                        this.is_active[3]=true;
                    }else{
                        this.tip="";
                        document.getElementById(tag2).placeholder = "";
                        this.add_if=true;
                        this.is_active[3]=false;
                    }
                }else{
                    document.getElementById(tag2).placeholder = "";
                    this.add_if=false;
                    this.is_active[3]=false;
                }
            },

            //
            addMsg(){
                let x = document.getElementById("text1").value;
                let y = document.getElementById("text2").value;
                let z = document.getElementById("text3").value;
                let m = document.getElementById("text4").value;
                if (x === "" || y === "" || z === "" || m === "") {
                    this.tip = ""
                } else {
                   this.checkIn('text1','2-10','[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0);
                   this.checkIn('text2','8-10','[a-zA-Z0-9]{8,10}$',1);
                   this.checkIn('text3','6-16','[a-zA-Z0-9_]{6,16}$',2);
                   this.checkPwd('text3','text4');
                    if ( this.add_if === true ) {
                        this.createUsers();
                    }
                }
            },

            // success
            successDelete(response){
                if (response.body.result === 1) {
                    console.log("success delete");
                    this.cleanData();
                } else if (response.body.result === 0) {
                    console.log("fail delete");
                }
            },

            failDelete(){
                console.log("error delete");
            },

            deleteUsers(index){
                this.delete_user.user_id = this.user_data[index].user_id;
                this.HttpPostForm(this.delete_url,this.delete_user,this.successDelete,this.failDelete);
            },


            // 
            openCha(index){
                this.show_change = true;
                this.back_index = index;
                this.back_username = this.user_data[index].user_name;
                this.back_account = this.user_data[index].account;
                this.tip='';
                this.is_active[0]=false;
                this.is_active[2]=false;
                this.is_active[3]=false;

            },

            closeCha(){
                this.show_change=false;
                this.back_username='';
                this.back_account='';
                this.pwd='';
                this.confirm_pwd = '';
                this.back_index='';
            },

            successModify(response){
                if( response.body.result === 1 ){
                    console.log("success modify!");
                    this.cleanData();
                    this.show_change= false; //
                } else if( response.body.result === 0 ){
                    console.log("fail modify!");
                }
            },

            failModify(){
                console.log("error modify!");
            },

            //
            modifyUsers(){
                this.modify_user.name=this.back_username;
                this.modify_user.pwd=this.pwd;
                this.modify_user.user_id=this.user_data[this.back_index].user_id;
                this.HttpPostForm(this.modify_url,this.modify_user,this.successModify,this.failModify);
                /*this.$http.post(
                    this.modify_url,
                    this.modify_user,
                    { emulateJSON: true },
                    { headers:{'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8'} }
                ).then(function (response) { //
                    this.success_modify(response);
                    this.AfterSuccess(response);
                },function () {
                    this.fail_modify();
                })*/
            },

            //
            confirmChange(){
                let x=document.getElementById("chan2").value;
                let y=document.getElementById("chan3").value;
                let z=document.getElementById("chan4").value;
                if (x === "" || y === "" || z === "" ) {
                    this.tip = ""
                } else {
                    this.checkIn('chan2','2-10','[\u4e00-\u9fa5a-zA-Z0-9]{2,10}$',0);
                    this.checkIn('chan3','6-16','[a-zA-Z0-9_]{6,16}$',2);
                    this.checkPwd('chan3','chan4');
                    if (this.add_if === true) {
                        this.modifyUsers();
                    }
                }
            },


            preP(){
                this.page = --this.page;
                this.cleanData();
                console.log("pre:"+this.page);
            },

            nextP(){
                this.page= ++this.page;
                this.cleanData();
                console.log("next:"+this.page);
            },

            skiP(p){
                this.page = p;
                this.cleanData();
                console.log("skip:" + this.page);
            },

            //,
            cleanData(){
                this.user_data.splice(0, this.user_data.length);
                this.getUsers(this.page);
            }
        }
    }

</script>



<style>
    /**/
    .table-container{
        padding-top: 60px;
        margin:auto;
        width: 52%;
    }

    /**/
    .table-box{
        background-color: #f7e1b5;  /**/
        border-style: solid;    /**/
        border-color: sienna ;  /**/
        border-collapse: collapse;  /**/
        width:800px;
        table-layout: fixed;    /**/

    }

    /**/
    th,td{
        padding: 5px;   /**/
        border-style: solid;    /**/
        border-color: sienna;  /**/
        text-align: center; /**/

        /**/
        font-family: ;
        color: dimgrey;
        font-size: 20px;
    }

    /**/
    .user-text{
        text-align: left;

        /**/
        font-family: ;
        font-size: 20px;
        font-weight: bold;
        color: dimgrey;
    }

    /**/
    .btn-general{
        background-color:sienna;   /**/
        color: #F2F2F2; /**/
        width:70px;
        height: 28px;
        border-radius: 8px;
        border-color: transparent;
    }

    /**/
    .create-user{
        margin-top: 1%;
        margin-bottom: 1%;
    }

    .input-pwd{
        background: transparent;    /**/
        border-color: transparent;  /**/
        text-align: center;
    }

    .dialog {
        width: 480px;
        position: fixed;
        left: 50%;
        top: 6em;
        transform: translateX(-50%);
        z-index: 2000;
        visibility: hidden;
        backface-visibility: hidden;
        perspective: 1300px;
    }

    .dialog-active{
        visibility: visible;
    }

    .dialog-active .dialog-content {
        position: relative;
        opacity: 1;
        transform: rotateY(0);
    }

    .dialog-active ~ .dialog-overlay {
        opacity: 1;
        visibility: visible;
    }

    .dialog-content {
        border-radius: 2px;
        background: #fff;
        overflow: hidden;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        transition: .5s ease-in-out;
        opacity: 0;
        transform-style: preserve-3d;
        transform: rotateY(-70deg);
    }

    .dialog-header {
        background: #fff;
        margin-top: 3%;
    }

    .dialog-title {
        color: dimgrey;
        font-family: ;
        margin-left: 35%;
        margin-right: 33%;
        font-size: 25px;
        text-align: center;
        font-weight: 200;
        line-height: 1em;

    }

    .dialog-body {
        padding: 2em;
    }

    .dialog-footer {
        padding: 3% 0;
        text-align: center;
        border-top: 1px solid rgba(0, 0, 0, 0.1);
    }

    .dialog-overlay {
        content: "";
        position: fixed;
        visibility: hidden;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1000;
        opacity: 0;
        background: rgba(0, 0, 0, 0.5);
        transition: all .6s;
    }

    .close {
        display: inline-block;
        width: 2rem;
        height: 2rem;
        position: absolute;
        top: .5rem;
        right: .5rem;
        transition: .8s ease all;
         -moz-transition: .8s ease all;
         -webkit-transition: .8s ease all;
        border: none;
        border-radius: 3px;
        color: #333;
        text-decoration: none;
        box-sizing: border-box;
         -webkit-box-sizing: border-box;
    }

    .close:hover {
        transition: .8s ease all;
         -moz-transition: .8s ease all;
         -webkit-transition: .8s ease all;
    }

    .close .iconfont {
        font-size: 2rem;
        color: black;
    }

    .rotate {
        cursor: pointer;
    }

    .rotate:hover {
        transition: transform 1.0s ease;
         -moz-transition: -moz-transform 1.0s ease;
         -webkit-transition: -webkit-transform 1.0s ease ;
    }

    .close .iconfont {
        font-size: 2rem;
        color: black;
    }

    .iconfont {
        font-size: 33px;
        font-style: normal;
        margin-top: -0.8%;
         -webkit-font-smoothing: antialiased;
         -webkit-text-stroke-width: 0.2px;
         -moz-osx-font-smoothing: grayscale;
    }
    .span-motal{
        font-family: ;
        font-size: 20px;
        font-weight: bold;
        color: dimgrey;

        text-align:right;
        padding-left: 10%;
    }

    .input-motal{
        width: 45%;
        right: 15%;
        position:fixed;
        text-align: center;
    }
    .warn-tip{
        color: #ff0000;
        font-size: 18px;
        text-align:center;
        font-family: ;
    }

    .warnBorder {
        border: 2px solid red;
    }

    .strap{

    }




</style>


