<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
    <!---->
    <div>
        <div class="head">
            <h3></h3>
        </div>
        <div class="login">
            <div class="input-ac-pwd">
                <div class="input-ac">
                    <span class="text1"> </span>
                    <input type="text" class="username" v-model="account" id="username" @blur="CheckUser()" v-bind:class="{ warnborder: Active1 }">
                </div>
                <div class="input-pwd">
                    <span class="text2"> </span>
                    <input type="password" class="password" v-model="pwd" id="pwd" @blur="CheckPwd()" v-bind:class="{ warnborder: Active2 }">
                </div>
                <div class="warning-login">
                    <span id="warning" v-model="warning">{{warning}}</span>
                </div>
            </div>
            <div class="code">
                <input type="text" placeholder="" class="verification-code-input" v-model="v">
                <img src="" id="v_picture" class="code-img" onclick="this.src=this.src+'?'+(new Date()).getTime()" alt="">
            </div>
            <div class="auto">
                <!---->
                <input type="checkbox" id="save-password"  class="save-password-checkbox" @click="Auto()">
                <span class="save-password-word" ></span>
            </div>
            <button class="login-button" @click="Login()" v-bind:disabled="disabled">  </button>
            <!--<button @click="test"></button>-->
        </div>
    </div>
</template>

<script type="text/javascript">
import store from './store'
//mock
//let Mock=require('mockjs');
//Mock.mock(
//  '/ancient_books/login.action',{
//        "name": "",
//        "su|0-1": 0,
//        "result|0-1": 1,
//        "info": ""
//    }
//);
 export default{

     data(){
         return{
             login_url: '/ancient_books/login.action',
             code_url: '/ancient_books/get_v_picture.action',
             judge_code_url: '/ancient_books/get_v_picture.action',
             autologin_url: '/ancient_books/get_user_info.action',
             warning:'',
             account: '',
             pwd: '',
             v: '',
             Active1:false,
             Active2:false,
             disabled: true,
             auto: false,
             object: {}
         }
     },

     //
     beforeMount:function () {
         this.OnloadToken();
     },

     methods: {
//         Test(){
//            this.$store.commit("login_show")
//         },

         /**
          *   
          */

         CheckUser(){
             this.disabled=false;
             this.warning = "";
             this.Active1=false;
             let success1=this.checkuser(this.account);
             if (success1){
                 this.warning= "";
                 this.Active1=false;
                 this.disabled=false;
             }else {
                 this.warning = "";
                 this.Active1=true;
             }
         },


         /**
          *   
          */

         CheckPwd(){
             this.disabled=false;
             this.warning = "";
             this.Active2=false;
             let success2=this.checkpwd(this.pwd);
             if (success2){
                 this.warning= "";
                 this.Active2=false;
                 this.disabled=false;
             }else {
                 this.warning = "";
                 this.Active2=true;
             }
         },
         CreateVPicture(){
             document.getElementById("v_picture").src='/ancient_books/get_v_picture.action';
         },

         // token
         OnloadToken(){
             this.$http.get('/ancient_books/getToken.action').then(function (response) {
                 console.log("token");
                 this.Token = response.body.token;
                 console.log(this.Token);
                 if (this.Token.length != 0){
                     this.AutoLogin();
                     this.CreateVPicture()
                 }
             },function () {
                 alert("error")
             })
         },

         Auto() {
           this.auto = !this.auto
         },
         //
         LoginSuccess(response){
             if (response.body.result == 0) {
                 document.getElementById("v_picture").src='/ancient_books/get_v_picture.action'+'?'+(new Date()).getTime();
                 alert(response.body.info);
             }
             if (response.body.result == 1) {
                 if (response.body.su == 1) {
                     this.$router.push({path: '/super_user'});
                     console.log("Token"+this.Token)
                 }
                 if (response.body.su == 0) {
                     this.$router.push({path: '/user'});
                 }
             }
         },
         //
         LoginFail(){
           alert("error")
        },
         //
         Login() {
             //object
             this.object.account=this.account;
             this.object.pwd=this.pwd;
             this.object.v=this.v;
             this.object.auto=this.auto;
             console.log("token"+this.Token);
             // vue-resource
             this.HttpPostForm(this.login_url,this.object,this.LoginSuccess,this.LoginFail);
         },
         //
//         GetCode() {
//             this.$http.get(this.code_url).then(function () {
//             })
//         },
         //
         AutoLogin() {
             this.$http.get(this. autologin_url).then(function (response) {
                 if (response.body.result==1) {
                     if (response.body.su == 1)
                         this.$router.push({path: '/user'});
                     if (response.body.su = 0)
                         this.$router.push({path: '/super_user'});
                 }
                 if (response.body.result==0)
                     this.$router.push({path: '/login'});
             },function () {
                 alert("error")
             })
         }
     }
 }
</script>

<style>
    .warning-login{
        width: 300px;
        height: 30px;
        font-size:12px;
        color:red;
        margin-top: 10px;
    }
    .warnborder{
        border:2px solid red;
    }
    .head{
        margin-left: 615px;
        margin-top: 120px;
        width: 300px;
        letter-spacing: 20px;
        font-size: 20px;
    }
    .login{
        background-color: #d9d9d9;
        width: 300px;
        height: 320px;
        margin-left: 590px;
        margin-top: 20px;
    }
    .input-ac-pwd{
        margin: 30px;
        display: inline-block;
    }
    .input-ac{
         margin-bottom: 10px;
     }
    .input-pwd{
          margin-top: 20px;
      }

    .text1{
    }
    .username{
        margin-left: 20px;
    }
    .text2{
        margin-left: -70px;
    }
    .password{
        margin-left: 20px;
    }
    .code{
        margin-top: -30px;
        margin-left: 60px;
    }
    .verification-code-input{
        height: 30px;
        width: 76px;
        text-align: center;
        border: none;
    }
    .code-img{
        margin-left: 30px;
        width: 76px;
        height: 30px;
        vertical-align:bottom;
        border: none;
    }
    .auto{
        margin-left: 50px;
        margin-top: 10px;
    }
    .save-password-checkbox{
        vertical-align: text-bottom;
    }
    .save-password-word{
        font-size: 12px;
    }
    .login-button{

        margin-top: 20px;
        margin-left: 75px;
        width: 160px;
        height: 30px;
        color: gray;
        background-color: white;
        border: none;
    }

</style>


